* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  background-color: #c2c2c2;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}

.movie {
  width: 420px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: white;
  margin: 10px;
  padding: 15px;
}

.movie img {
  width: 150px;
  margin-right: 10px;
  position: relative;
}

.movie__data {
  display: flex;
  flex-direction: column;
}

.movie__data * {
  margin: 5px 0;
}

.movie__data .movie__title {
  font-size: 20px;
  font-weight: 700;
}

.genres {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.genres * {
  margin-right: 10px;
}
